export default class Options{
    constructor(obj = {name: undefined,nickname: undefined, birthday: undefined}){
        this.name = obj.name;
        this.nickname = obj.nickname;
        this.birthday = obj.birthday;
    }
    
    getOptions(){
        return {
            name: this.name,
            nickname: this.nickname,
            birthday: this.birthday
        }; 
    }
    
    setOptions(obj){
        this.name = obj.name || this.name;
        this.nickname = obj.nickname || this.nickname;
        this.birthday = obj.birthday || this.birthday;
    }

    storeCookie(path = '/', expires){
        document.cookie = `fullname=${this.name};expires=${expires};`;
        document.cookie = `nickname=${this.nickname};expires=${expires};`;
        document.cookie = `birthday=${this.birthday};expires=${expires};`;
        document.cookie = `path=${path};expires=${expires};`;
    }
    resetCookie(){
        document.cookie = 'fullname=;expires=Thu, 01 Jan 1970 00:00:00 GMT';
        document.cookie = 'nickname=;expires=Thu, 01 Jan 1970 00:00:00 GMT';    
        document.cookie = 'birthday=;expires =Thu, 01 Jan 1970 00:00:00 GMT';
        document.cookie = "path=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
        document.cookie = 'expires=;expires=Thu, 01 Jan 1970 00:00:00 GMT';
    }
    getCookie(value){
        const USER_INFO = document.cookie;
        if(USER_INFO == "") return undefined;
        else{
            return USER_INFO.split('; ')
            .find(info => info.startsWith(value))
            .split('=')[1]
        }

    }
    displayCookie(){
        return decodeURIComponent(document.cookie);
    }
}